.page-footer__contacts-form {
  background-color: #f2f2f2;
}
.page-footer__contacts {
  margin: 0 auto;
}
.page-footer__copyright {
  background-color: #f2f2f2;
}
.page-footer__social {
  width: 280px;
  margin: 0 auto;
  text-align: center;
  background-color: #f2f2f2;
}
@media (min-width: @tablet-width) {
  .page-footer__contacts {
    background-color: #ffffff;
  }
  .page-footer__social {
    width: 768px;
    margin: 0 auto;
    padding: 60px 0;
    display: flex;
    justify-content: space-between;
  }
}
@media (min-width: @desktop-width) {
  .page-footer__social {
    width: 1220px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
  }
  .page-footer__contacts {
    position: relative;
  }
  .page-footer__copyright {
    margin-top: 108px;
  }
}
