.profile {
  width: 280px;
  margin: 0 auto;
  padding: 0;
}
.profile__data-item li{
  list-style: none;
  padding-bottom: 15px;
}
.profile__data-item {
  padding: 0;
  display: block;
  margin-bottom: 35px;
}
.profile__contacts {
  padding: 0;
  margin: 0;
  margin-bottom: 13px;
  list-style: none;
}
.profile__label {
  align-items: center;
  display: flex;
  font-family: @font-oswald;
  text-transform: uppercase;
  font-size: 16px;
  color: #000000;
}
.profile__info-text {
  border: 1px solid #d9d9d9;
  font-family: @font-oswald;
  text-transform: uppercase;
  width: 200px;
  margin-left: 14px;
  padding: 11px 13px;
}
.profile__main-form {
  margin-bottom: 27px;
}
.profile__info-text-years {
  border: 1px solid #d9d9d9;
  width: 137px;
  padding: 11px 17px;
  margin-left: 13px;
}
.profile__info-text-weight {
  border: 1px solid #d9d9d9;
  padding: 11px 21px;
  width: 163px;
  margin-left: 12px;
}
.profile__weight-items {
  padding: 0;
  margin-bottom: 31px;
}
.profile__weight-items li{
  list-style: none;
  padding-bottom: 25px;
  width: 280px;
}
.profile__weight-title {
  text-transform: uppercase;
  font-family: @font-oswald;
  font-size: 16px;
  color: #000000;
  padding-left: 30px;
}
.profile__intro {
  margin: 0;
  font-family: @font-oswald;
  font-size: 24px;
  color: #000000;
  margin-bottom: 30px;
}
.profile__data-option {
  padding-bottom: 18px;
}
.profile__info {
  border: 1px solid #d9d9d9;
  font-family: @font-oswald;
  text-transform: uppercase;
  resize: none;
  padding: 10px;
  width: 260px;
  height: 130px;
  margin-bottom: 23px;
}
.profile__intro-extra {
  margin-bottom: 38px;
}
.profile__additionally-items {
  padding: 0;
  margin: 0;
  margin-bottom: 18px;
}
.profile__additionally-items li {
  list-style: none;
  padding-bottom: 24px;
  padding-left: 29px;
}
.profile__additionally-title {
  font-size: 16px;
  text-transform: uppercase;
  font-family: @font-oswald;
}
.profile__icon {
  width: 187px;
  padding: 11px 11px;
}
.profile__label-svg-phone {
  position: absolute;
  width: 21px;
  right: 32px;
  height: 21px;
}
.profile__label-svg-mail {
  position: absolute;
  width: 18px;
  height: 14px;
  right: 35px;
}
.profile__btn-request {
  width: 280px;
  padding: 10px 0px;
  margin-bottom: 22px;
  font-family: @font-oswald;
  font-size: 16px;
  text-transform: uppercase;
  color: #ffffff;
  border: none;
  background-color: #68b738;
}
.profile__necessarily {
  font-size: 14px;
  font-family: @font-arial;
}
.profile__form {
  margin-bottom: 76px;
}
@media (min-width: @tablet-width) {
  .profile {
    width: 768px;
    margin: 0 auto;
    padding: 0;
    padding-left: 30px;
    padding-right: 30px;
  }
  .profile__label {
    font-size: 20px;
  }
  .profile__info-text {
    padding: 12px 14px;
  }
  .profile__data-item {
    display: block;
    margin: 0;
    margin-top: -10px;
    margin-bottom: 87px;
  }
  .profile__data-item li {
    padding-bottom: 22px;
    padding-left: 41px;
  }
  .profile__info {
    width: 645px;
    min-height: 175px;
  }
  .profile__info-text-weight {
    width: 505px;
    padding: 12px 23px;
  }
  .profile__info-text-years {
    width: 470px;
  }

 .profile__additionally-items {
    display: flex;
    flex-wrap: wrap;
    width: 550px;
    padding: 0;
  }
.profile__additionally-items li{
    width: 192px;
}
.profile__main-form {
  margin-bottom: 82px;
}
.profile__intro {
  font-size: 36px;
  width: 708px;
  margin: 0 auto;
  margin-bottom: 76px;
  margin-right: 38px;
}
.profile__weight-title {
  font-size: 20px;
}
.profile__weight-items li {
  padding-bottom: 29px;
  padding-left: 98px;
}
}
@media (min-width: @desktop-width) {
  .profile {
    width: 1220px;
    margin: 0 auto;
    padding: 0;
    padding-left: 30px;
    padding-right: 30px;
  }
  .profile__data-option {
    padding-bottom: 21px;
    padding-left: 0;
  }

  .profile__main-form {
    display: flex;
    justify-content: space-between;
  }
  .profile__main-form ul {
    width: 570px;
    margin-top: -11px;
    display: block;
  }
  .profile__info-text {
    padding: 14px 21px;
    width: 523px;
  }
  .profile__info-text-weight {
    width: 425px;
    padding: 14px 24px;
    margin-left: 20px;
    border: 1px solid #d9d9d9;
  font-family: @font-oswald;
  text-transform: uppercase;
  }
  .profile__info-text-years {
    width: 395px;
    padding: 14px 20px;
    border: 1px solid #d9d9d9;
    font-family: "Oswal", sans-serif;
    text-transform: uppercase;
    margin-left: 16px;
    margin-right: 0;
    display: block;
  }
   .profile__data-item li {
    width: 570px;
    padding-left: 0;
  }
  .profile__info {
    width: 1220px;
  }
  .profile__data-item {
    margin: 0;
  }
  .profile__weight-items {
    border: 1px solid;
    margin-top: -5px;
  }
  .profile__additionally-items {
    display: flex;
    width: 1220px;
    justify-content: space-between;
  }
  .profile__weight-items li {
    padding-bottom: 0;
    padding-left: 56px;
    padding-top: 32px;
  }
  .profile__intro {
    margin: 0;
    margin-bottom: 73px;
  }
  .profile__main-form {
    margin-bottom: 40px;
  }
  .profile__contacts {
    margin: 0;
    list-style: none;
    display: flex;
    justify-content: space-between;
    padding: 0;
    margin-bottom: 44px;
  }
}
