.nutrition {
  width: 280px;
  margin: 0 auto;
  margin-bottom: 43px;
}
.nutrition__list {
  list-style: none;
  margin: 0;
  padding: 0;
  font-family: @font-oswald;
  color: #000000;
}
.nutrition__list img {
  position: relative;
  z-index: 10;
}
.nutrition__list li {
  display: flex;
  margin: 0;
  padding-bottom: 20px;
  font-size: 14px;
  font-family: @font-arial;
  position: relative;
}
.nutrition__image {
  width: 60px;
  height: 60px;
  background-color: #68b738;
  }
.nutrition__info {
 width: 200px;
 margin: 0;
 padding-top: 5px;
 padding-left: 20px;
 font-family: @font-arial;
 font-size: 14px;
 line-height: 1.29;
}
.nutrition__title {
  line-height: 1.67;
  font-weight: 400;
  font-size: 36px;
  font-family: @font-oswald;
  margin: 0;
  margin-bottom: 23px;
}
.nutrition__number {
  display: none;
}
@media (min-width: @tablet-width) {
  .nutrition {
    width: 708px;
    margin-bottom: -15px;
  }
  .nutrition__title  {
    margin: 0 auto;
    width: 708px;
    margin-bottom: 53px;
    font-size: 60px;
    font-weight: normal;
  }
  .nutrition__image {
    width: 80px;
    height: 80px;
    margin-bottom: 37px;
  }
  .nutrition__info {
    width: 244px;
    padding: 0;
    font-size: 16px;
    z-index: 100;
    position: relative;
    opacity: 0.7;
  }
  .nutrition__number {
    color: #f2f2f2;
    font-size: 281px;
    position: absolute;
    top: -55px;
    right: 80px;
    z-index: 1;
    margin: 0;
    display: inline;
    font-family: @font-oswald;
    font-weight: normal;
    font-style: normal;
  }
  .nutrition__list {
    width: 708px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .nutrition__list li{
    display: block;
    width: 324px;
    margin-bottom: 124px;
  }
}
@media (min-width: @desktop-width) {
  .nutrition {
    width: 1220px;
    margin-bottom: 126px;
  }
  .nutrition__title  {
    margin: 0;
    margin-bottom: 53px;
 }
  .nutrition__list {
    width: 1220px;
    display: flex;
    position: relative;
  }
  .nutrition__image {
    margin-bottom: 32px;
  }
  .nutrition__number {
    color: #f2f2f2;
    font-size: 281px;
    position: absolute;
    top: -45px;
    right: 0;
    z-index: 1;
    margin: 0;
    display: inline;
  }
  .nutrition__list li{
    display: block;
    width: 245px;
  }
}
