.main-nav {
  position: relative;
  width: 280px;
  margin: 0 auto;
}
.main-nav__list {
  margin: 10px 0 0;
  padding: 0;
  list-style: none;
}
@media (max-width: @mobile-width-only) {
  .main-nav {
    &--closed {
      display: none;
    }
    // .main-nav--closed {
    //   font-size: 0;
    //   border: none;
    //   position: absolute;
    //   top: -30px;
    //   right: 20px;
    //   z-index: 2;
    //   display: block;
    //   width: 25px;
    //   height: 19px;
    //   background-color: #ffffff;
    //   cursor: pointer;
    //   content: "";
    //   background-image: url("/img/icon-close.svg");
    // }
    &--active {
      display: block;
    }
    &--nojs {
      .page-header__toggle {
        display: none;
      }
    }
  }
  .main-nav__toggle {
    font-size: 0;
    border: none;
    position: absolute;
    top: -30px;
    right: 20px;
    z-index: 2;
    display: block;
    width: 25px;
    height: 19px;
    background-color: #ffffff;
    cursor: pointer;
    content: "";
    background-image: url("/img/icon-mobile-menu.svg");
  }
}
@media (min-width: @tablet-width) {
  .main-nav {
    max-width: 516px;
    width: 100%;
    margin: 0 0 0 auto;
  }
  .main-nav__toggle {
    display: none;
  }
}
@media (min-width: @desktop-width) {
  .main-nav__wrapper {
    width: 570px;
    display: flex;
  }
  .main-nav {
    max-width: 570px;
    width: 100%;
    margin: 0 0 0 auto;
  }
}
