.img-wrap {
  position: relative;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #68b738;
  margin: 0 20px 0 0;
}
@media (min-width: @tablet-width) {
  .img-wrap {
    position: absolute;
    right: -9px;
    top: 12px;
    width: 200px;
    height: 200px;
  }
}
@media (min-width: @desktop-width) {
  .img-wrap {
    position: relative;
    width: 100px;
    height: 100px;
    margin-left: 49px;
    margin-right: 67px;
  }

}

