.social {
  background-color: #f2f2f2;

}
.social__img {
  padding: 39px;
}
.social__list {
  list-style: none;
  display: flex;
  justify-content: center;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
  padding: 15px 0;
  margin: 0 auto;
}
.social__list li {
  padding-right: 10px;
}
@media (min-width: @tablet-width) {
  .social__img {
    padding: 0;
  }
  .social__image-logo {
    width: 33.333333%;
    text-align: start;
  }
  .social__list {
    padding: 0;
    border: none;
    width: 33.333333%;
    justify-content: center;
  }
}
@media (min-width: @desktop-width) {
  .social__list {
    padding: 0;
  }
}
