.example {
  background-color: #f2f2f2;
  margin-bottom: 49px;
}
.example__main {
  width: 280px;
  margin: 0 auto;
}
.example__title {
  font-size: 36px;
  line-height: 1.67;
  color: #000000;
  font-weight: 400;
  font-family: @font-oswald;
  margin: 0;
  margin-bottom: 23px;
}
.example__slider {
  display: flex;
  margin-top: 23px;
  width: 197px;
  margin: 0 auto;
  text-transform: uppercase;
  justify-content: space-between;
}
.example__intro {
  font-size: 14px;
  line-height: 1.29;
  color: #000000;
  font-weight: 400;
  font-family: @font-arial;
  margin: 0;
  margin-bottom: 24px;
}
.example__info {
  margin-bottom: 11px;
  display: flex;
  justify-content: space-between;
}
.example__nutrition {
  margin: 0;
  margin-bottom: 59px;
  text-align: center;
  font-size: 14px;
  line-height: 1.43;
  font-weight: 400;
  text-transform: uppercase;
}
.example__number {
  padding: 14px 40px;
  margin: 0;
  text-transform: uppercase;
  display: block;
  text-align: center;
  font-size: 24px;
  line-height: 1;
  border-radius: 3px;
  border: solid 1px #cdcdcd;
}
.example__number-item-2 {
  padding: 14px 22px;
}
.example__table-info {
  font-size: 12px;
  margin-top: -12px;
  margin-left: 22px;
  box-sizing: border-box;
  padding: 0 7px;
  width: 78px;
  text-align: center;
  background-color: #f2f2f2;
}
@media (min-width: @tablet-width) {
  .example__main {
    width: 708px;
    margin: 0 auto;
  }
  .example__title {
    font-size: 60px;
    margin-bottom: 45px;
  }
  .example__intro {
    font-size: 16px;
    line-height: 1.5;
    margin-bottom: 52px;
  }
  .example__container {
    display: flex;
    justify-content: space-between;
  }
  .example__number {
    padding: 19px 57px;
    font-size: 30px;
    margin-right: 39px;
  }
  .example__number-item-2 {
    padding: 19px 29px;
  }
  .example__table-info {
    width: 100%;
    margin-left: -21px;
    font-size: 14px;
    font-family: @font-arial;
    background-color: #ffffff;
  }
  .example__nutrition {
    font-size: 20px;
    padding-bottom: 0px;
    margin: 0;
    padding-top: 4px;
  }
}
@media (min-width: @desktop-width) {
  .example {
    padding: 1px 0 0;
    margin-bottom: 150px;
  }
  .example__main {
    width: 1220px;
    margin: 0 auto;
    padding-bottom: 58px;
  }
  .example__info {
    justify-content: flex-start;
  }
  .example__info-main {
    width: 570px;
  }
  .example__title {
    margin-top: -156px;
    margin-bottom: 128px;
    font-size: 60px;
  }
  .example__intro {
    width: 425px;
    margin-bottom: 90px;
  }
  .example__container {
    display: block;
  }
  .example__container-main {
    display: flex;
    justify-content: space-between;
  }
  .example__number {
    font-size: 30px;
    padding: 17px 56px;
  }
  .example__img-wrapper {
    width: 570px;
    text-align: center;
  }
  .example__nutrition {
    text-align: left;
  }
  .example__img {
    margin-top: -180px;
    margin-bottom: 30px;
  }
  .example__slider {
    display: flex;
  }
}
