.filter {
  position: relative;
}
.filter__input-checkbox+label {
  position: relative;
}
.filter__input-checkbox+label::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 30px;
  height: 30px;
  border: solid 1px #cdcdcd;
  border-radius: 50%;

}
.filter__input-checkbox:checked+label::after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 12px;
  height: 12px;
  background-color: gray;
  border-radius: 50%;
}
