.contacts__title {
  font-size: 16px;
  line-height: 1.25;
  font-weight: normal;
  font-style: normal;
  text-transform: uppercase;
}
.contacts__address {
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  box-sizing: border-box;
  font-family: @font-arial;
  line-height: 1.43;
}
.contacts__map-img {
  width: 100%;
  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("/img/bg-map-mobile@2x.png");
  }
}

.contacts__main {
  display: flex;
  margin: 0 auto;
  width: 280px;
}
.contacts__main p {
  width: 140px;
  margin: 0;
}
@media (min-width: @tablet-width) {
  .contacts__main {
    width: 640px;
    background-color: white;
    margin: 0 auto;
    justify-content: space-between;
    p {
      width: 192px;
    }
  }
  .contacts__title {
    font-size: 20px;
  }
  .contacts__address {
    font-size: 16px;
  }
}
@media (min-width: @desktop-width) {
  .contacts__map {
    z-index: 1;
  }
  .contacts__title {
    font-size: 20px;
  }
  .contacts__main {
    margin-left: 111px;
    padding: 65px 76px;
    position: relative;
    z-index: 10;
    width: 440px
  }
  .contacts__map-img {
    position: absolute;
    top: -93px;
  }
}
