.programs {
  width: 280px;
  margin: 0 auto;
}
.programs__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.programs__list li {
  margin: 0 0 20px;
  padding: 20px;
}
.programs__container-title {
  display: flex;
  // padding: 0 20px;

  padding-top: 6px;
  padding-bottom: 23px;
  position: relative;
  // &::before {
  //   z-index: 1;
  //   position: absolute;
  //   content: "";
  //   width: 50px;
  //   height: 50px;
  //   top: 0;
  //   left: 0;
  //   background-color: #68b738;
  //   border-radius: 50px;
  // }
}
.programs__item {
  background-color: #f2f2f2;
}
.programs__image {
  position: absolute;
  left: 50%;
  top: 50%;
  max-width: none;
  transform: translate(-50%, -50%);
}
.programs__title {
  margin: 0;
  align-self: center;
  text-transform: uppercase;
  font-family: @font-oswald;
  font-size: 24px;
  font-weight: normal;
  color: #000000;
}
.programs__description {
  margin: 0;
  margin-bottom: 18px;
  font-size: 14px;
  line-height: 1.29;
  padding-bottom: 20px;
  border-bottom: 2px solid #d9d9d9;
  font-size: 14px;
  font-family: @font-arial;
  color: #000000;
}
.programs__catalog {
  font-family: @font-oswald;
  text-transform: uppercase;
  font-size: 16px;
  text-decoration: none;
  color: #000000;
}
@media (min-width: @tablet-width) {
  .programs {
    width: 708px;
    margin: 0 auto;
  }
  .programs__container-description {
    padding: 0px 41px 0px 0px;
    width: 383px;
    box-sizing: border-box;
  }
  .programs__item {
    position: relative;
  }
  .programs__image {
    width: 141px;
    height: 194px;
  }
  .programs__container-title {
    padding: 0;

  }
  .programs__image {
    margin: 0;
  }
  .programs__image-item-2 {
    width: 268px;
    height: 113px;
  }
  .programs__title {
    font-size: 36px;
    margin-bottom: 23px;
  }
  .programs__description {
    margin: 0;
    font-size: 16px;
    line-height: 1.5;
    padding-bottom: 55px;
    border-bottom: none;
  }
  .programs__catalog {
    font-size: 20px;
  }
  .programs__list li {
    margin-bottom: 33px;
    display: block;
    padding: 74px 52px 54px;
  }
}
@media (min-width: @desktop-width) {
  .programs {
    width: 1220px;
    margin: 0 auto;
    margin-bottom: 20px;
  }
  .programs__container-title {
    display: flex;
    margin: 0 auto;
    width: 570px;
    margin-bottom: 46px;
    // &::before {
    //   width: 100px;
    //   height: 100px;
    //   top: 9px;
    //   left: -40px;
    //   border-radius: 100px;
    // }
}
.programs__title {
  font-size: 36px;
  margin-top: 38px;
}
  .programs__container-description {
    margin: 0;
    padding-left: 51px;
    width: 540px;
    margin-top: 9px;
  }
  .programs__description {
    padding-bottom: 27px;
  }
  .programs__image {
    position: relative;
    // top: 2px;
    // right: 65px;
    width: 139px;
    height: 100px;
  }
  .programs__list {
    padding: 0;
    display: flex;
    justify-content: space-between;
 }
  .programs__list li {
    padding: 36px 0 64px;
    width: 570px;
    display: block;

  }
}
