.additional {
  width: 280px;
  margin: 0 auto;
}
.additional__items {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-bottom: 37px;
}
.additional__item {
  padding: 10px 0;
  padding-bottom: 20px;
  border-top: 1px solid;
}
.additional__btn-order {
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  padding: 10px 0px;
  display: block;
  width: 100%;
  margin: 0 auto;
  background-color: #68b738;
  color: #ffffff;
  font-size: 16px;
  font-family: "Oswal", sans-serif;
}

.additional__title {
  margin-bottom: 34x;
  font-size: 24px;
  line-height: 1;
  font-weight: 400;
}
.additional__slogan {
  padding-bottom: 12px;
  padding-top: 4px;
  margin: 0;
  font-size: 16px;
  line-height: 1;
  font-weight: 400;
  text-transform: uppercase;
}
.additional__info {
  margin: 0;
  font-size: 14px;
  line-height: 1;
  font-weight: 400;
  font-family: @font-arial;
}
.additional__price {
  margin: 0;
  font-family: @font-arial;
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
}
.additional__info-main {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
}
.additional__main {
  margin-bottom: 63px;
  margin-top: 33px;
}
@media (min-width: @tablet-width) {
  .additional {
    width: 708px;
  }
  .additional__items {
    margin-bottom: 75px;
  }
  .additional__container-main {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;
  }
  .additional__item {
    padding: 26px 37px;
    border: 1px solid #cdcdcd;
  }
  .additional__info-main {
    align-self: center;
  }
  .additional__info-main {
    margin: 0;
  }
  .additional__info {
    margin-right: 50px;
    font-size: 16px;
  }
  .additional__price {
    font-size: 16px;
  }
  .additional__slogan {
    font-size: 20px;

  }
  .additional__title {
    font-size: 36px;
    margin: 0;
    padding-left: 40px;
    margin-bottom: 80px;
  }
}
@media (min-width: @desktop-width) {
  .additional {
    width: 1220px;
    margin-bottom: 73px;
  }
  .additional__main {
    display: flex;
    justify-content: space-between;
  }
  .additional__items {
    width: 895px;
  }
  .additional__item {
    padding: 20px 0;
  }
  .additional__container-main {
    margin: 0;
    width: 650px;
    padding-top: 10px;
  }
  .additional__info-main {
    width: 285px;
    margin-right: 38px;
  }
  .additional__title {
    padding: 0;
    margin-bottom: 79px;
  }
  .additional__btn-order {
    margin: 0;
    width: 245px;
  }
  .additional__item {
    padding: 15px 0px;
    display: flex;
    justify-content: space-between;
  }
}
