.site-list__item {
  font-family: @font-oswald;
  line-height: 24px;
  font-size: 20px;
  text-transform: uppercase;
  font-weight: normal;
  font-style: normal;
  text-align: center;
  list-style: none;
  border-bottom: 1px solid #cdcdcd;
  a {
    padding: 15px 0;
    display: block;
    text-decoration: none;
    color: #000000;
  }
  }
  .site-list__item a:focus,
  .site-list__item a:hover {
    opacity: 0.5;
  }
  @media (min-width: @tablet-width) {
  .site-list {
      justify-content: flex-end;
      display: flex;
      }
      .site-list__item  {
        font-size: 18px;
        border: none;
        a {
          color: #000000;
        }
      }
      .site-list li {
        margin-left: 36px;
      }
  }
  @media (min-width: @desktop-width) {
      .site-list {
      z-index: 1000;
      position: relative;
      width: 570px;
      display: flex;
      }
      .site-list__item-main  {
        a {
          color: #ffffff;
        }
      }
      .site-list__item {
        font-size: 20px;
      }
      .site-list li {
        margin-left: 34px;
    }
  }
