.selection {
  width: 280px;
  margin: 0 auto;
  color: #000000;
  font-family: @font-oswald;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 43px;
  margin-top: 30px;
}
.selection__title {
  font-size: 36px;
  line-height: 1.67;
  font-weight: 400;
  margin-top: 41px;
  margin: 0;
  margin-bottom: 26px;
}
.selection__intro {
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
  margin: 0;
  box-sizing: border-box;
  padding-right: 20px;
}
@media (min-width: @tablet-width) {
  .selection {
    width: 708px;
    margin-bottom: 77px;

  }
  .selection__title {
    font-size: 60px;
    margin-top: 40px;
    margin-bottom: 33px;
  }
  .selection__intro {
    font-size: 20px;
    width: 365px;
    padding-left: 40px;
    line-height: 1.5;
    padding-right: 0;
  }

}
@media (min-width: @desktop-width) {
  .selection {
    width: 1220px;
  }
  .selection__title {
    margin-top: 54px;
    margin-bottom: 33px;
  }
  .selection__intro {
    padding-left: 0;
  }
}
