.show {
  position: relative;
  background: none;
  text-align: center;
  border: 1px solid #e7e7e7;
  padding: 75px 0px 11px;
  margin-bottom: 43px;
  &::before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  width: 31px;
  height: 31px;
  opacity: 0.2;
  background-image: url("/img/icon-plus.svg");
  }
}
.show__title {
  margin: 0;
  margin-bottom: 10px;
  text-transform: uppercase;
  color: #000000;
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  font-family: @font-oswald;
}
.show__intro {
  margin: 0;
  margin-bottom: 22px;
  color: #000000;
  font-size: 12px;
  font-family: @font-arial;
}
.show__btn-all {
  font-family: @font-oswald;
  font-size: 16px;
  display: block;
  text-transform: uppercase;
  text-align: center;
  margin: 0 auto;
  padding: 10px 0px;
  width: 100%;
  background-color: #f2f2f2;
  color: #000000;
  border: none;
}
@media (min-width: @tablet-width) {
  .show__title {
    margin-top: 73px;
    margin-bottom: 25px;
    box-sizing: border-box;
    padding: 0 92px;
    font-size: 20px;
  }
  .show__intro {
    box-sizing: border-box;
    padding: 0 50px;
    font-size: 16px;
    margin: 20px;
    margin-bottom: 84px;
  }
}
@media (min-width: @desktop-width) {
  .show__title {
    padding: 0 50px;
    margin: 0;
    padding-top: 76px;
  }
  .show__intro {
    padding: 0 30px;
    margin-bottom: 72px;
  }
}
