.banner {
  color: #ffffff;
  z-index: 100;
  position: relative;
  width: 280px;
  margin: 0 auto;
  margin-bottom: 20px;
  display: block;
  font-weight: 400;
  font-family: @font-oswald;

  &::after {
    z-index: 10;
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 305px;
    background-repeat: no-repeat;
    background-image: url("/img/index-background-mobile@1x.jpg");
    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("/img/index-background-mobile@2x.jpg");

    }
  }
}
.banner__btn-selection:focus,
.banner__btn-selection:hover {
  background-color: #5eaa2f;
}
.banner__btn-selection:active {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
}
.banner__container {
  position: relative;
  z-index: 105;
}
.banner__slogan {
  margin: 0;
  padding-top: 27px;
  margin-bottom: 25px;
  text-align: center;
  font-size: 36px;
  line-height: 1;
  font-weight: normal;
}
.banner__intro {
  margin: 0;
  margin-bottom: 28px;
  text-align: center;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 1;
}
.banner__feed-image {
  z-index: 100;
  position: relative;
}
.banner__btn-selection {
  font-family: @font-oswald;
  text-transform: uppercase;
  font-size: 14px;
  text-decoration: none;
  padding: 14px 0px;
  display: block;
  line-height: 1;
  text-align: center;
  color: #ffffff;
  width: 100%;
  background-color: #68b738;
}

@media (min-width: @tablet-width) {
  .banner {
    width: 708px;
    margin-top: 36px;
    display: flex;
    flex-direction: column;
    margin-bottom: 5px;
    color: #000000;
    &::after {
      display: none;
    }
  }
  .banner__slogan {
    text-align: start;
    margin-bottom: 40px;
    font-size: 60px;
    padding-top: 23px;
  }
  .banner__intro {
    text-align: start;
    margin-bottom: 50px;
    font-size: 20px;
  }
  .banner__container {
    width: 427px;
    margin-left: 88px;
  }
  .banner__feed-image {
    margin: -82px 0px 0px -1px;
    order: 2;
    width: 709px;
    height: 609px;
    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("/img/index-can-tablet@2x.jpg");
    }
  }
  .banner__picture {
    order: 2;
  }
  .banner__btn-selection {
    z-index: 101;
    width: 195px;
    font-size: 20px;
    padding: 15px 25px;
    margin: 0 88px;
    position: relative;
  }
}
@media (min-width: @desktop-width) {
  .banner {
   width: 1220px;
   position: relative;
   margin-bottom: 273px;
   margin-top: 97px;
  }
  .banner__feed-image {
    top: -71px;
    left: 450px;
    position: absolute;
    margin: 0;
    width: 552px;
    height: 499px;
    object-fit: contain;
    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("/img/index-can-desktop@1x.png");
    }
  }
  .banner__container {
   width: 510px;
   margin-left: 80px;
  }
  .banner__slogan {
    padding-top: 16px;
  }
}
