.products {
  width: 280px;
  margin: 0 auto;
}
.products__title-main {
  margin: 0 auto;
  width: 280px;
  font-size: 36px;
  margin-top: 39px;
  margin-bottom: 36px;
  font-family: @font-oswald;
  font-weight: normal;
  color: #000000;
}
.products__items li{
  list-style: none;
}

.products__title {
  margin: 0;
  margin-bottom: 1px;
  box-sizing: border-box;
  padding-top: 20px;
  padding-right: 44px;
  font-size: 16px;
  line-height: 1.25;
}
.products__container-item {
  width: 140px;
}
.products__post {
  width: 140px;
  margin-bottom: 5px;
}
.products__info td {
  font-family: @font-arial;
  color: #000000;
  font-size: 12px;
  line-height: 1.33;
  font-weight: 400;
  opacity: 0.8;
}
.products__info {
  width: 144px;
}
.products__value {
  text-align: left;
}
.products__field {
  text-align: right;
}
.products__container {
  display: flex;
}
.products__item {
  margin-bottom: 28px;
  // padding: 0 20px;
}
.products__items {
  margin: 0;
  padding: 0;
  list-style: none;
}
.products__title a {
  text-transform: uppercase;
  font-family: @font-oswald;
  color: #000000;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
  text-decoration: none;
  font-weight: normal;
}

.products__btn-order {
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  padding: 10px 0px;
  display: block;
  width: 100%;
  margin: 0 auto;
  background-color: #68b738;
  color: #ffffff;
  font-size: 16px;
  font-family: @font-oswald;
}
@media (min-width: @tablet-width) {
  .products {
    width: 708px;
    margin: 0 auto;
    margin-bottom: -75px;
  }
  .products__container {
    display: block;
    text-align: center;
  }
  .products__image {
    display: flex;
    margin-bottom: 36px;
    margin-top: -79px;
    height: 204px;
    align-items: flex-end;
  }
  .products__title-main {
    font-size: 60px;
    margin-top: 53px;
    margin-bottom: 160px;
    width: 708px;
  }

  .products__container-item {
    width: 324px;
    margin-bottom: 28px;
  }
  .products__title a {
    font-size: 20px;
  }
  .products__title {
    box-sizing: border-box;
    padding: 0 87px;
    margin-bottom: 17px;
  }
  .products__btn-order {
    width: 249px;
  }
  .products__post {
    text-align: start;
    border-bottom: 1px solid #cdcdcd;

  }
  .products__items {
    padding: 0;
    width: 708px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
  }
  .products__items li {
    margin-bottom: 142px;
    padding-bottom: 40px;
  }
  .products__info {
    margin: 0 auto;
    margin-bottom: 32px;
    width: 248px;
    border-collapse: collapse;
  }
  .products__item:nth-child(2n){
    margin-right: 0;
  }
  .products__item {
    padding: 0 0 20px;
    width: 324px;
    background-color: #f2f2f2;
    margin-right: 58px;
  }

  .products__info td {
    font-size: 16px;
    padding-bottom: 8px;
  }
}
@media (min-width: @desktop-width) {
  .products {
    width: 1300px;
    margin: 0 auto;
    margin-bottom: -67px;
  }
  .products__items {
    padding: 0;
    width: 1220px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
  }
  .products__item {
    margin-right: 78px;
    padding: 0 0 25px;
    width: 245px;
  }
  .products__info {
    width: 160px;
  }
  .products__item:nth-child(2n) {
    margin-right: 78px;
  }
  .products__item:nth-child(4n){
    margin-right: 0;
  }
  .products__container-item {
    width: 245px;
  }
  .products__title {
    padding: 0 50px;
  }
  .products__btn-order {
    width: 169px;
  }
  .products__title-main {
    width: 1220px;
    margin-top: 71px;
    margin-bottom: 162px;
}
}
