.page-header {
  width: 280px;
  margin: 0 auto;
}
.page-header__logo-image {
  margin-top: 12px;
}
.page-header__logo:hover,
.page-header__logo:focus {
 opacity: 0.7 ;
}
.page-header-container {
  position: relative;
  margin: 0 auto;
  width: 280px;
  margin-bottom: 15px;
}
@media (max-width: @mobile-width-only) {
  .page-header__toggle {
    font-size: 0;
    border: none;
    position: absolute;
    top: 10px;
    right: 20px;
    z-index: 2;
    display: block;
    width: 25px;
    height: 19px;
    background-color: #ffffff;
    cursor: pointer;
    content: "";
    background-repeat: no-repeat;
    background-image: url("/img/icon-mobile-menu.svg");
  }
}
@media (min-width: @tablet-width) {
  .page-header {
    width: 768px;
  }
  .page-header__logo {
    width: 203px;
    height: 60px;
    background-repeat: no-repeat;
  }
  .page-header-container {
    width: 708px;
    display: flex;
    align-items: center;
    padding-top: 17px;
 }
}
@media (min-width: @desktop-width){
  .page-header {
    width: 1300px;
    margin: 0 auto;
    z-index: 100;
    position: relative;
  }
  .page-header__logo {
    background-size: cover;
    width: 203px;
    height: 60px;
  }

    .page-header-bg {
      width: 1300px;
      margin: 0 auto;
      z-index: 100;
      position: relative;
      &::after {
        z-index: 10;
        position: absolute;
        content: '';
        width: 720px;
        height: 696px;
        top: 0;
        right: -70px;
        background-image: url("/img/index-background-desktop@1x.jpg");
        @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
          background-image: url("/img/index-background-desktop@2x.jpg");
        }
      }
 }
  .page-header-container {
    width: 1220px;
    padding-top: 30px;
 }
}
